:root {
  --orange: #f7901f;
  --white: #ffffff;
  --gray: rgba(0,0,0,.5);
  --ff-nunito: "Nunito Sans", sans-serif;
}

/* Box sizing rules */
*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Remove default margin */
body,
h1,
h2,
h3,
h4,
p,
figure,
blockquote,
dl,
dd {
  margin: 0;
}

/* Remove list styles on ul, ol elements with a list role, which suggests default styling will be removed */
ul[role=list],
ol[role=list] {
  list-style: none;
}

/* Set core root defaults */
html:focus-within {
  scroll-behavior: smooth;
}

/* Set core body defaults */
body {
  min-height: 100vh;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
}

/* A elements that don't have a class get default styles */
a:not([class]) {
  text-decoration-skip-ink: auto;
}

/* Make images easier to work with */
img,
picture {
  max-width: 100%;
  display: block;
}

/* Inherit fonts for inputs and buttons */
input,
button,
textarea,
select {
  font: inherit;
}

/* Remove all animations, transitions and smooth scroll for people that prefer not to see them */
@media (prefers-reduced-motion: reduce) {
  html:focus-within {
    scroll-behavior: auto;
  }

  *,
*::before,
*::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}
html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0 auto;
  padding: 0;
  font-size: 1rem;
  max-width: 120rem;
  min-width: 20rem;
  font-family: var(--ff-nunito);
}

.container {
  max-width: 120rem;
  margin: 0 auto;
  width: 90%;
}

.preloader {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1001;
  background: var(--orange);
  display: flex;
  justify-content: center;
}
.preloader .preloader-logo {
  width: 80%;
  max-width: 31.25rem;
}
.preloader .preloader-logo path,
.preloader .preloader-logo rect {
  stroke-linecap: square;
  fill: rgba(255, 255, 255, 0);
}

.fadeInUp {
  transform: translate(0, 2.1875rem);
  opacity: 0;
}

.main-header {
  width: 100%;
  backdrop-filter: saturate(180%) blur(20px);
  background: rgba(255, 255, 255, 0.72);
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1000;
}
.main-header:after {
  content: "";
  background: rgba(0, 0, 0, 0.16);
  height: 0.0625rem;
  width: 100%;
  position: absolute;
  top: 100%;
  left: 0;
}
.main-header .container {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
  padding: 0.5rem 0;
}
.main-header .logo {
  width: 7.5rem;
  display: block;
}
.main-header .logo img {
  display: block;
  width: 100%;
}
.main-header .nav-toggle {
  background: transparent;
  display: flex;
  width: 2rem;
  border: 0;
  padding: 0;
  cursor: pointer;
}
.main-header .nav-toggle .toggle-lines {
  stroke-linecap: square;
  height: 3rem;
}
.main-header .nav-toggle .toggle-lines rect {
  fill: var(--orange);
}
.main-header .main-nav {
  flex: 100% 1;
  align-items: center;
  overflow: hidden;
}
.main-header .main-nav .nav {
  list-style-type: none;
  display: flex;
  flex-direction: column;
  -webkit-padding-start: 0;
  margin: 0;
}
.main-header .main-nav .nav-link {
  display: block;
  text-decoration: none;
  color: var(--gray);
  text-align: center;
  padding: 0.5rem 0;
}
.main-header .main-nav .nav-link:hover {
  color: var(--orange);
}

@media (min-width: 768px) {
  .main-header .logo {
    width: 9.375rem;
  }
}
@media (min-width: 992px) {
  .main-header .container {
    flex-flow: row wrap;
    justify-content: flex-start;
  }
  .main-header .nav-toggle {
    display: none;
  }
  .main-header .main-nav {
    display: flex;
    flex-basis: auto;
    height: auto !important;
  }
  .main-header .main-nav .nav {
    flex-direction: row;
    margin-left: auto;
    gap: 1.875rem;
  }
}
.hero {
  height: 100vh;
}
.hero .hero-wrapper {
  position: relative;
  justify-content: center;
  height: 100%;
  display: flex;
  align-items: center;
  overflow: hidden;
}
.hero .hero-img {
  background: url("/dist/images/hero.jpg") no-repeat center center;
  background-size: cover;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
}
.hero .hero-text {
  text-align: center;
}
.hero .hero-text h1 {
  color: var(--white);
  font-size: 8.5333333333vw;
  line-height: 1;
  margin-bottom: 0.9375rem;
}
.hero .hero-text p {
  color: var(--white);
  width: 90%;
  max-width: 56.25rem;
  margin: 0 auto;
  font-size: 4.2666666667vw;
}

@media (min-width: 768px) {
  .hero .hero-text h1 {
    font-size: 4rem;
  }
  .hero .hero-text p {
    font-size: 1.5rem;
  }
}